import React, { Fragment, useEffect, useRef, useState } from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'
import { useWindowWidth } from '../../hooks'
import { Location } from '@reach/router'

const Navbar = styled.nav`
    height: 100%;
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    // position: relative;
    // border: 1px solid #f99;
`

const MenuItem = styled(Link)(({ theme }) => `
    // flex: 1;
    outline: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
    text-decoration: none;
    text-transform: uppercase;
    color: ${ theme.color.black };
    padding: 1rem 2rem;
    transition: color 250ms, border-bottom-color 250ms;
    background-color: transparent;
    &.active {
        color: ${ theme.color.primary.dark };
    }
`)

const Marker = styled.div(({ theme, x, width }) => `
    position: absolute;
    left: 0;
    top: 94px;
    width: 100%;
    height: 8px;
    border-radius: ${ theme.border.radius };
    background-color: ${ theme.color.primary.main };
    transition: opacity 250ms;
    opacity: 1.0;
    transition: transform 150ms ease-out, max-width 250ms;
    transform: translateX(${ x }px);
    max-width: ${ width }px;
`)

export const Menu = ({ items }) => {
    const { windowWidth } = useWindowWidth()
    const [activeIndex, setActiveIndex] = useState()
    const [markerProperties, setMarkerProperties] = useState(0)
    const activeMenuItem = useRef()
    
    const getMarkerCoordinates = () => {
        if (activeMenuItem.current) {
            const { x, y, width } = activeMenuItem.current.getBoundingClientRect()
            setMarkerProperties({ x, y, width })
        }
    }

    useEffect(() => {
        activeMenuItem.current = document.querySelector('.active')
        getMarkerCoordinates()
    }, [typeof window !== 'undefined' && window.location.pathname, windowWidth])

    return (
        <Navbar>
            <Location>
                {
                    ({ location }) => {
                        console.log(location.pathname)
                        return (
                            <Fragment>
                                { items.map(item => <MenuItem key={ item.text } to={ item.path } activeClassName="active" partiallyActive={ location === item.path }>{ item.text }</MenuItem>) }
                                <Marker x={ markerProperties.x } top={ markerProperties.y } width={ markerProperties.width } />
                            </Fragment>
                        )
                    }
                }
            </Location>
        </Navbar>
    )
}